
import React, { PureComponent } from "react";
import ReactApexChart from "react-apexcharts";
import ApexCharts from "apexcharts";
import moment from "moment";
export default class SentimentChart extends PureComponent {
  constructor(props) {
    super(props);

    const getData = this.props.contentAnalyticsData.newSeries[0]['data']
    const lastCategory = getData && Object.keys(getData).length && this.props.contentAnalyticsData.newSeries[0]['data'].length-1; // Use dynamic category if needed
    const seriesData   = getData && Object.keys(getData).length && this.props.contentAnalyticsData.newSeries[0]['data'];

      console.log(Object.keys(this.props.contentAnalyticsData.newSeries).length,'seriesDatajksdklfj')

    this.state = {
      series: this.props.contentAnalyticsData.newSeries,
      options: {
        colors: ["#546FED"],
        chart: {
          id: "area-datetime",
          toolbar: { show: false },
          type: "line",
          animations: {
            enabled: true,
            easing: "linear",
            dynamicAnimation: {
              speed: 1000,
            },
          },
          height: 350,
          zoom: { enabled: false },
        },
        legend: {
          show: true,
          position: "top",
          itemMargin: {
            horizontal: 20,
            vertical: 0,
          },
        },
        dataLabels: { enabled: false },
        markers: { 
        size: 4, style: "hollow" },
        stroke: {
          width: [4],
          curve: "straight",
        },
        xaxis: {
          type: "datetime",
          min: this.props.contentAnalyticsData?.unixDate[0],
          max: this.props.contentAnalyticsData?.unixDate[0] - 364000000, // One day later (or adjust based on your needs)
          //max: '1827241540330',
          labels: { 
            formatter: (value) => {
              return moment(value).subtract(1, 'days').format('HH:mm')
            }
          },
          tooltip: {
            shared: true,
            intersect: false, // Allows tooltip to show when hovering near the points
            formatter: function (value) {
              return moment(value).subtract(1, 'days').format('DD-MM-YY HH:mm')
            },
          }
          ,tickPlacement: 'on', // Optional: places ticks directly on the x-axis labels

        },
        yaxis: {
          min: 0,   // Fixing the y-axis minimum value to 0
          max: 100, // Fixing the y-axis maximum value to 100
          labels: {
            formatter: function (value) {
              return Math.round(value); // Rounds the value to the nearest integer
            }
          }
        },
      
        annotations: {
          points: [
            {
              x: seriesData ? lastCategory && seriesData[lastCategory][0] : 0, // Dynamically set based on last category
              y: seriesData ? lastCategory && seriesData[lastCategory][1] : 0, // Last data point
              marker: {
                size: 8, // Highlight the last marker
                fillColor: "#546FED",
                strokeColor: "#2545d7",
                shape: "circle",
                cssClass: "blinking-marker", // Apply custom CSS class for blinking effect
              },
              label: {
                borderColor: "#546FED",
                offsetY: -10, // Move the label higher to avoid overlap
                offsetX: -10, // Move the label higher to avoid overlap
                style: {
                  color: "#546FED",
                  background: "transparent", // Make label background transparent
                  zIndex: 0, // Lower z-index to ensure marker is on top
                },
                //text: `${lastCategory && seriesData[lastCategory][1]}`,
              },
            }],
          yaxis: [
            {
              y: 30,
              y2: 0,
              borderColor: "transparent",
              fillColor: "#f7caca", // Color for 0-30 range
            },
            {
              y: 60,
              y2: 30,
              borderColor: "transparent",
              fillColor: "#f9debe", // Color for 30-60 range
            },
            {
              y: 100,
              y2: 60,
              borderColor: "transparent",
              fillColor: "#cfe7d5", // Color for 60-100 range
            },
          ],
        },
        tooltip: {
          enabled: true,
          x: { format: "dd MMM yyyy" },
          custom: ({ series, seriesIndex, dataPointIndex, w }) => {

             const seriesData = series[0][dataPointIndex]

            return (
              seriesData<=30 ?
              `<div class="apexcharts-tooltip-item bg-red"><img src="https://cloud.tagbox.com/react-app/media/icons/emoji/emoji5.svg">` +
              `<div><strong>Negative</strong><br/>` + `${seriesData}%` +`</div></div>`
                : seriesData<=60 ? 
               `<div class="apexcharts-tooltip-item bg-yelow"><img src="https://cloud.tagbox.com/react-app/media/icons/emoji/emoji3.svg">` + 
               `<div><strong>Neutral</strong><br/>` + `${seriesData}%` + `</div></div>`
                :
               `<div class="apexcharts-tooltip-item bg-green"><img src="https://cloud.tagbox.com/react-app/media/icons/emoji/emoji1.svg">` +
               `<div><strong>Positive</strong><br/>` + `${seriesData}%` + `</div></div>`
            );
          },
        },
        grid: {
          show: false, // Enable or disable the entire grid (optional)
        },
      },
      responsive: [
        {
          // breakpoint: 991,
          options: {
            chart: {
              height: 300,
            },
          },
          // breakpoint: 767,
          options: {
            chart: {
              height: 280,
            },
          },
        },
      ],
    };
  }

  render() {
    const { contentAnalyticsData } = this.props;

    const lastDate =
      contentAnalyticsData?.unixDate[
        contentAnalyticsData?.unixDate?.length - 1
      ];
    if (lastDate) {
      const chart = ApexCharts.getChartByID("area-datetime2");
      if (chart) {
        chart.zoomX(
          contentAnalyticsData?.unixDate[0],
          contentAnalyticsData?.unixDate[lastDate - 1]
        );
      }
    }


    return (
     <> 
      <ReactApexChart
        options={this.state.options}
        series={this.state.series}
        type="line"
        height={350}
      />
           
     </> 
    );
  }
}
